<template>
  <div class="redemption-success">
    <scroll :pullup="true"
            @scrollToEnd="loadMore"
            :data="list" class="redemption-scroll" ref="redemptionScroll">
      <div>
        <img class="success-img" src="./images/redemption_success.png" alt="">
        <p class="success-p">恭喜您购买成功!</p>
        <div class="operation-wrapper">
          <div @click="toMoney" class="left">继续购买</div>
          <div @click="toRedemption" class="right">查看订单</div>
        </div>
        <div class="tuijian">
          <you-like class="you-like"></you-like>
          <div class="list-wrapper">
            <div class="list">
              <goods-item @toDetail="toDetail" @loadImage="loadImage"
                          v-for="item in list"
                          :key="item.id" class="list-item"
                          :data="item">
              </goods-item>
            </div>
          </div>
        </div>
        <loading v-show="showLoad" class="loading"
                 :title="loadingTitle"></loading>
      </div>
    </scroll>
  </div>

</template>

<script>
  import * as core from '../../api/search.js'
  import tool from '../../common/js/util'
  import YouLike from '../../base/you-like/you-like'
  import Loading from '../../base/loading/loading'
  import GoodsItem from '../../base/goods-item/goods-item.vue'

  import Scroll from '../../base/scroll/scroll'

  export default {
    name: 'redemption-success',
    data() {
      return {
        recommendData: [],
        list: [],
        value: '',
        currentPage: 1,
        pageSize: 10,
        loadingTitle: '正在加载...',
        showLoad: true,
        noSearch: false
      }
    },
    components: {
      YouLike,
      Loading,
      Scroll,
      GoodsItem
    },
    created() {
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)

      this.getLikeGoods({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
      })
    },
    methods: {
      toDetail(e) {
        this.$router.push({path: '/commodityDetails', query: {commodityId: e}})
      },
      loadImage() {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$refs.redemptionScroll.refresh()
        }, 500)
      },
      loadMore() {
        if (!this.showLoad && !this.hasMore) {
          this.getLikeGoods({
            currentPage: this.currentPage,
            pageSize: this.pageSize,
          })
        }
      },
      toMoney() {
        if(this.$route.query.isVirtualGoods == 'Y'){
           this.$router.push({path: '/serviceCenter'})
        }else{
          this.$router.push({path: '/'})
        }
      },
      toRedemption() {
        this.$router.push({path: '/orderForm'})
      },
      getLikeGoods(opts) {
        this.showLoad = true
        core.likeGoods(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.list = this.list.concat(res.result.data)
            this.loadingTitle = '';
            this.showLoad = false;
            this.currentPage++;
            if (this.list.length >= res.result.amount) {
              this.hasMore = true
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/variable.styl"
  .redemption-success
    text-align center
    .redemption-scroll
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background-color #f5f5f5
      .success-img
        padding-top 3.094rem
        width 4.188rem
      .success-p
        font-size 1.25rem
        padding-top 1.063rem
      .operation-wrapper
        width 82.61%
        margin 0 auto
        padding-top 3rem
        display flex
        justify-content space-between
        div
          width 8.375rem
          height 2.75rem
          line-height 2.75rem
          border-radius 0.313rem
          color $jujibao-theme
          font-size 1.125rem
          border 1px solid #ff4800
        .right
          color #fff
          background-color #ff4800
      .tuijian
        padding 0 4.27%
        .you-like
          margin 4rem 0 0.375rem 0
        .list-wrapper
          text-align left
          width 100%
          padding 0.625rem 0 1rem
          box-sizing border-box
          margin 0 auto
          .list
            display flex
            justify-content space-between
            flex-wrap wrap
            padding-bottom 1rem
            .list-item
              width 49.43%
              overflow hidden
              border-radius 0.25rem
              margin-bottom 0.25rem
              break-inside: avoid;
</style>



